// 导入title
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import VXETable from '@/components/common/Table';
import roleOperation from '@/views/main/RoleManagement/roleOperation.vue';
import SearchBox from '@/components/common/SearchBox';
import PageAuthoritySetting from '@/views/main/RoleManagement/PageAuthoritySetting';
import { STATUS_CODE } from '@/utils/constantsFile';
// 导入所需接口
import {
  roleGetRoleListAction,
  roleUpdateByIdAction,
  roleInsertAction,
  roleDeleteByIdsAction,
  updateRoleRelationshipUserBody,
  userGetAllAction
} from '@/api/role';
// import {
//   userGetUserListAction
// } from '@/api/management';
import { showWarningMessage } from '@/utils/message';
import {
  typeOptions
} from '@/utils/search';
export default {
  name: 'RoleManagement',
  data() {
    return {
      // 搜索框有关
      isSearchBoxShow: false,
      typeOptions,

      treeData: [],
      checkAll: false,
      checkedCities: [],
      cities: [],
      allUsers: [], // 保存所有用户
      uids: [], // 添加用户uids
      isIndeterminate: true,
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      tableLoading: false,
      dialogVisible: false,
      adddialogVisible: false,
      titleName: '',
      othertitlename: '',
      flag: 0,
      roleid: '', // 添加用户roleid
      usernames: [],
      form: {
        roleName: ''
      },
      rolerules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { max: 10, message: '长度不得大于10个字符' }
        ]
      },
      formLabelWidth: '100px',
      tableDatas: [],
      RoleTableName: 'role',
      comfirmflag: '',
      rolecheck: [],
      // 表头
      tableColumns: [
        {
          type: 'radio',
          align: 'center',
          visible: true,
          width: '50'
        },
        // { type: 'checkbox', width: '50', align: 'center' },
        {
          field: 'roleName',
          title: '角色名称',
          minWidth: '100',
          align: 'center'
          // sortable: true
        },
        {
          field: 'userList',
          title: '用户列表',
          minWidth: '300',
          align: 'center',
          formatter: this.formatUser
          // sortable: true
        },
        {
          title: '操作',
          width: '120',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row, column }) => {
              // const statat = row.sbState
              return [
                <el-dropdown
                  placement="bottom"
                  size="small"
                  onCommand={(command) => this.handleCommand(command, row)}
                >
                  <span class="operation-column">
                    操作
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item
                      class="operation-column"
                      command="setAuthority"
                    >
                      权限设置
                    </el-dropdown-item>
                    <el-dropdown-item
                      class="operation-column"
                      command="addUser"
                    >
                      添加用户
                    </el-dropdown-item>
                    <el-dropdown-item
                      class="operation-column"
                      command="modify"
                    >
                      修改
                    </el-dropdown-item>
                    <el-dropdown-item
                      class="operation-column"
                      command="delete"
                    >
                      删除
                    </el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
                /* <el-tooltip class="item" effect="dark" content="权限设置" placement="top-start">
                  <i
                    style="margin-top:5px"
                    onClick={ () => this.rolesetting(row, column) }
                    class="icon-set-authority cursor-pointer">
                  </i>
                </el-tooltip>,
                <el-tooltip class="item" effect="dark" content="添加用户" placement="top-start">
                  <i
                    style="margin-top:5px"
                    onClick={ () => this.adduser(row, column) }
                    class="icon-add-user cursor-pointer">
                  </i>
                </el-tooltip>,
                <el-tooltip class="item" effect="dark" content="修改" placement="top-start">
                  <i
                    style="margin-top:5px"
                    onClick={ () => this.modify(row, column) }
                    class="icon-table-edit cursor-pointer">
                  </i>
                </el-tooltip>,
                <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
                  <i
                    style="margin-top:5px"
                    onClick={ () => this.delete(row, column) }
                    class="icon-table-delete cursor-pointer">
                  </i>
                </el-tooltip> */
              ];
            }
            // header: ({ column }) => {
            //   return [
            //     <span
            //       style={{
            //         display: 'flex',
            //         justifyContent: 'center',
            //         alignItems: 'center'
            //       }}
            //     >
            //       <span>{column.title}</span>
            //       <el-button
            //         type="text"
            //         size="medium"
            //         onClick={() => this.openCustomizeColumnsDialog()}
            //         style={{
            //           position: 'absolute',
            //           right: '20px',
            //           padding: '6px 0'
            //         }}
            //       >
            //         <i class="el-icon-s-tools" style={{ float: 'right' }}></i>
            //       </el-button>
            //     </span>
            //   ];
            // }
          }
        }
      ],
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total'],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      }
    };
  },
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    roleOperation,
    SearchBox,
    PageAuthoritySetting
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },

    /**
     * @description:获取表格数据
     * @param {array} conditionArr1 条件数组一，即赋给conditionJson1的条件
     * @param {number} groupOperator 条件数组一和二的逻辑运算符，1为and，2为or
     * @param {array} conditionArr2 条件数组二，即赋给conditionJson2的条件
     * @return:
     * @author: Fizz
     */
    findList(conditionArr1 = [], groupOperator = 1, conditionArr2 = []) {
      this.tableLoading = true;
      const { currentPage, pageSize } = this.tablePage;
      const conditionJson = {
        current: currentPage,
        size: pageSize
      };
      roleGetRoleListAction({}, conditionJson)
        .then(res => {
          const data = res.rows;
          this.tableDatas = data;
          this.tablePage.total = res.total;
          setTimeout(() => {
            this.tableLoading = false;
          }, 500);
        });
    },
    // 切换页码
    onTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.findList();
    },
    // 修改方法
    modify() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.dialogVisible = true;
      this.titleName = '修改角色名称';
      this.comfirmflag = 'modify';
      this.form = { ...row };
    },
    // 删除方法
    delete() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      if (row.userList.length > 0) {
        this.$message({
          message: '该数据下还有用户，不可删除',
          type: 'warning'
        });
      } else {
        this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          roleDeleteByIdsAction({ ids: [row.roleId] })
            .then(res => {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.findList();
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    // 模态框显示
    addRole() {
      // if (name === 'addRole') {
      // 添加员工
      this.dialogVisible = true;
      this.comfirmflag = 'add';
      this.titleName = '添加角色';
      // } else if (name === 'batchDeleteEquipment') {
      //   // 批量删除关联设备
      // }
    },
    // 模态框确认按钮
    confirm() {
      // 验证其必填项
      this.$refs.roleForm.validate(valid => {
        if (!valid) {
        } else {
          if (this.comfirmflag === 'modify') {
            roleUpdateByIdAction(this.form)
              .then(res => {
                this.$message({
                  message: '修改成功',
                  type: 'success'
                });
                this.findList();
                this.dialogVisible = false;
              });
          } else if (this.comfirmflag === 'add') {
            roleInsertAction(this.form)
              .then(res => {
                this.$message({
                  message: '新增成功',
                  type: 'success'
                });
                this.findList();
                this.dialogVisible = false;
              });
          }
        }
      });
    },

    // 关闭模态框
    closeit() {
      this.$refs.roleForm.resetFields();
      Object.assign(this.$data.form, this.$options.data().form);
      this.findList();
    },
    /**
     * @description: （添加用户对话框）对话框关闭调用方法，清楚展示的数据
     * @param 无
     * @return: void
     * @author: cyb
     */
    clearAddUserDialog() {
      // 清空所有信息，防止干扰下一次打开对话框之后的初始化
      this.cities.splice(0);
      this.checkedCities.splice(0);
      this.uids.splice(0);
    },

    // 添加用户模态框显示
    adduser() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }

      this.adddialogVisible = true;
      this.roleid = row.roleId;
      this.getUserData(row);
    },
    /**
     * @description: (添加用户对话框)判断当前节点是否是用户
     * @param {object} node 节点对象
     * @return: boolean
     * @author: cyb
     */
    filterUser(node) {
      return this.allUsers.find(user => user.userName === node.name) !== undefined;
    },
    // 获取树
    getUserData(row) {
      this.checkedCities = [];
      var _that = this;
      userGetAllAction({}, {})
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            _that.cities = res.rows;
            var arr = [];
            if (row.userList.length > 0) {
              for (var i in row.userList) {
                arr.push(row.userList[i].userId);
              }
              this.checkedCities = arr;
            }
          }
        });
    },
    /**
     * @description: 获取显示哪个组件
     * @param {Number} flag，0为显示index组件，1为显示批量操作组件
     * @return:
     * @author: Fizz
     */
    goBack(flag, type) {
      this.flag = flag;
    },

    /**
     * @description:权限设置点击
     * @param {type}
     * @return:
     * @author: Fizz
     */
    rolesetting() {
      var row = {};

      if (!this.$refs.xGrid.getRadioRecord()) {
        showWarningMessage('请选择要操作的数据！');
        return false;
      } else {
        row = this.$refs.xGrid.getRadioRecord();
      }
      this.roleid = row.roleId;
      this.flag = 1;
      this.othertitlename = '权限设置';
    },
    // 权限页面勾选事件
    roleTableChooseCheck(data) {
      this.rolecheck = data;
    },
    /**
     * @description:批量删除
     * @param {type}ids
     * @return:
     * @author: Fizz
     */
    deteleall() {
      if (this.rolecheck.length === 0) {
        this.$message.error('请勾选后，再进行删除操作');
      } else {
        var arrdelete = [];
        this.rolecheck.map(role => arrdelete.push(role.roleId));
        this.$confirm('此操作将永久删除勾选数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          roleDeleteByIdsAction({ ids: arrdelete })
            .then(res => {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.findList();
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    /**
     * @description: （添加用户对话框）点击“保存”按钮，触发方法
     * @param 无
     * @return: void
     * @author: cyb
     */
    addUserConfirm() {
      const userRole = {
        roleId: this.roleid
      };
      // 如果没有勾选用户，不能保存
      if (this.checkedCities.length > 0) {
        userRole.userIdList = this.checkedCities;
      }
      console.log(userRole.userIdList);
      updateRoleRelationshipUserBody({}, userRole)
        .then(res => {
          this.$message({
            message: '新增成功',
            type: 'success'
          });
          this.adddialogVisible = false;
          this.findList();
        });
    },
    allSelect() {

    },
    /**
     * @description: （角色表）点击自定义列按钮，打开自定义列对话框
     * @param 无
     * @return: void
     * @author: cyb
     */
    openCustomizeColumnsDialog() {
      const columns = [];
      for (const column of this.tableColumns) {
        if (column.field || column.title === '操作') {
          columns.push({
            title: column.title,
            width: column.minWidth || '100',
            visible: true,
            required: ['角色名称', '操作'].includes(column.title)
          });
        }
      }
      const columnsStr = JSON.stringify(columns);
      this.$dialogCustomizeColumns.openDialog({
        isDialogShow: true,
        tempTableColumns: JSON.parse(columnsStr),
        defaultTableColumns: JSON.parse(columnsStr)
      });
    },

    /**
     * @description: （权限表）操作列点击方法
     * @param {string} command 菜单项
     * @param {object} row 行数据
     * @return: void
     * @author: cyb
     */
    handleCommand(command, row) {
      switch (command) {
        case 'setAuthority':
          this.rolesetting(row);
          break;
        case 'addUser':
          this.adduser(row);
          break;
        case 'modify':
          this.modify(row);
          break;
        case 'delete':
          this.delete(row);
          break;
        default:
          break;
      }
    },
    formatUser({ cellValue }) {
      const arr = [];
      for (var i in cellValue) {
        if (cellValue[i].realName) {
          arr.push(cellValue[i].realName);
        } else {
          arr.push(cellValue[i].userName);
        }
      }
      return arr.join('、');
    },
    refreshParentMenu() {
      this.$parent.$parent.$parent.getNavListData();
    }
  },
  created() {
    this.findList();
  }
};
