<template>
  <div v-if="flag === 0">
    <el-container>
      <!-- 表头部分 -->
      <el-header>
        <header-buttons  @buttonClickEvent="buttonClickEvent" cssType="1"></header-buttons>
        <!-- <HeaderTitle class="header-title" @searchButtonClick="showSearchCard">
          <div slot="head-operation" class="head-operation">
            <HeadOperationItem name="addRole" @onOprationItem="onModal" class="margin-right-10">
              <i slot="operation-icon" class="icon-add"></i>
              <span slot="operation-name">添加角色</span>
            </HeadOperationItem>
          </div>
        </HeaderTitle> -->
      </el-header>
      <el-main style="border-top: 1px solid #dedddd">
        <!-- 搜索框 -->
        <!-- <search-box
          :fields="tableColumns"
          :types="typeOptions"
          v-show="isSearchBoxShow"
          @searchBtnClick="findList"
        >
        </search-box> -->
        <!--表格部分  -->
        <div class="area-main" style="border-top: none">
          <VXETable
            :height="$options.filters.getTableHeight(160)"
            :tableName="'role'"
            :tableLoading="tableLoading"
            :tableDatas='tableDatas'
            :tableColumns='tableColumns'
            :tablePage='tablePage'
            :highlightCurrentRow="true"
            @onTablePageChange="onTablePageChange"
            @chooseCheck="roleTableChooseCheck"
            ref="xGrid"
          >
<!--             <div slot="tableOperation" class="tableOperations">
              <div class="tableOperation-item">
                <HeadOperationItem name="batchDeleteStaff" @onOprationItem="deteleall()">
                  <i slot="operation-icon" class="icon-batchDelete"></i>
                  <span slot="operation-name">批量删除</span>
                </HeadOperationItem>
              </div>
            </div> -->
          </VXETable>
        </div>
        <!-- 添加和修改角色模态框 -->
        <el-dialog
          :title="titleName"
          :visible.sync="dialogVisible"
          @close="closeit"
          width='30%'
          class="dialog-role"
        >
          <el-form :model="form" ref="roleForm" :rules="rolerules" @submit.native.prevent >
            <el-row>
              <el-col :span="20" :offset="1">
                <el-form-item label="角色名称：" :label-width="formLabelWidth" prop="roleName">
                  <el-input
                    v-model="form.roleName"
                    autocomplete="off"
                    size="small"
                  >
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button class="custom-button" type="primary" @click="confirm()">保存</el-button>
          </span>
        </el-dialog>
        <!-- 添加用户模态框 -->
        <el-dialog title="添加用户"
          :visible.sync="adddialogVisible"
          width='50%'
          class="dialog-add-user"
          @close="clearAddUserDialog"
        >
          <div class="main">
            <div class="main-r">
              <div class="scrollbar-container">
                <el-scrollbar style="height: 100%">
                  <el-checkbox-group
                    v-model="checkedCities"
                  >
                    <el-checkbox
                      v-for="(city, index) in cities"
                      :label="city.userId"
                      :value="city.userId"
                      :key="index"
                    >
                      {{city.realName?city.realName:city.userName}}
                    </el-checkbox>
                  </el-checkbox-group>
                </el-scrollbar>
              </div>
            </div>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button class="custom-button" @click="adddialogVisible = false">取消</el-button>
            <!-- <el-button class="custom-button" type="primary" @click="allSelect()">全选</el-button> -->
            <el-button class="custom-button" type="primary" @click="addUserConfirm()">保存</el-button>
          </span>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
  <!-- 权限设置页面 -->
  <div v-else-if="flag === 1">
     <page-authority-setting
      :role-id="roleid"
      @goBack="goBack"
      @refreshParentMenu="refreshParentMenu"
     >
     </page-authority-setting>
  </div>
</template>

<script>
import role from '@/assets/js/main/RoleManagement/index';
export default role;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/RoleManagement/index.less';
</style>
<style lang="less" scoped>
@import '../../../assets/less/public/operationColumn.less';
</style>
