<template>
  <el-container>
    <el-header>
      <HeaderTitle :titleName="titleName" :flag="flag">
      </HeaderTitle>
    </el-header>
    <el-main style="border-top: 1px solid #dedddd">
        <div class="main-cl">
          <div class="main-l">
            <div class="main-l-t"><span>模1块</span></div>
            <el-tree
              :data="treeData"
              node-key="id"
              :default-checked-keys="defkey"
              :props="defaultProps"
              default-expand-all
              show-checkbox
              style="overflow:auto;"
              ref="motree"
              @check-change="currentChange"
            >
            </el-tree>
          </div>
        </div>
        <el-card style="margin:10px;height:433px;" >
          <div class="main-r">
            <ul>
              <li>
                <div class="main-r-l">
                  <el-checkbox
                    :indeterminate="isIndeterminate"
                    v-model="checkAll"
                    @change="handleCheckAllChange"
                  >
                    用户管理
                  </el-checkbox>
                </div>
                <div class="main-r-r">
                  <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                    <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
                  </el-checkbox-group>
                </div>
              </li>
              <li>
                <div class="main-r-l">
                  <el-checkbox
                    :indeterminate="isIndeterminate"
                    v-model="checkAll"
                    @change="handleCheckAllChange"
                  >
                    用户管理
                  </el-checkbox>
                </div>
                <div class="main-r-r">
                  <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                    <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
                  </el-checkbox-group>
                </div>
              </li>
            </ul>
          </div>
        </el-card>
        <div>
            <el-button class="custom-button" type @click="goBack">返回</el-button>
            <el-button class="custom-button" type="primary" @click="comfirm" >保存</el-button>
        </div>
    </el-main>
  </el-container>
</template>

<script>
import rolebatch from '@/assets/js/main/RoleManagement/rolebatch';
export default rolebatch;
</script>

